<template>
  <div :class="isScroll ? 'scrollHead minnav' : 'minnav'">
    <i class="el-icon-menu" @click="drawer = true"></i>
    <div class="myname">Zhe(Betty) Ji</div>
    <el-drawer
      :visible.sync="drawer"
      direction="ltr"
      :modal="false"
      :size="size"
    >
      <div class="minTabs"><NavTabs tabPosition="left"></NavTabs></div>
    </el-drawer>
  </div>
</template>

<script>
import NavTabs from "@/components/NavTabs.vue";
export default {
  name: "minnav",
  components: { NavTabs },
  data() {
    return {
      drawer: false,
      isScroll: false,
      size: "50%"
    };
  },
  mounted() {
    window.addEventListener("scroll", this.scrollListen);
  },
  methods: {
    scrollListen: function() {
      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      this.isScroll = scrollTop >= 100 ? true : false;
    }
  }
};
</script>

<style scoped lang="less">
.minnav {
  width: 100%;
  height: 3.2rem;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1;
}
.scrollHead {
  background: #2b2b2b;
}
.scrollHead .myname {
  color: white;
}
.el-icon-menu {
  float: left;
  font-size: 1.5rem;
  line-height: 3.75rem;
  color: gray;
  margin-left: 0.5rem;
}
.myname {
  margin-left: 2.8rem;
  // font: italic 1.2rem/3.75rem Georgia, serif;
  font: normal 500 1.2rem/3.75rem 'Times New Roman', Times, serif;
}
.minTabs {
  /deep/ .el-tabs__nav-wrap {
    background: #212121;
  }
  /deep/ .el-tabs__item {
    color: white;
    font-family: sans-serif;
    font-size: 15px !important;
  }
  /deep/ .el-tabs__active-bar {
    background-color: transparent !important;
  }
}
/deep/.el-drawer {
  background: #212121 !important;
}
/deep/ .el-drawer__close-btn .el-dialog__close {
  color: white !important;
}
</style>



