<template>
  <div class="home">
    <el-row :gutter="10">
      <el-col :xs="24" :sm="10" :md="8" :lg="6" :xl="6">
        <div id="profile">
          <img src="../assets/profile.jpg" />
        </div>
      </el-col>
      <el-col :xs="24" :sm="14" :md="16" :lg="18" :xl="18">
        <div id="introduce">
          <transition :duration="{ enter: 1200, leave: 300 }" name="fade">
            <h1>Zhe(Betty) Ji</h1>
          </transition>
          <p>
            I am a PhD Candidate in Marketing at DeGroote School of Business,
            McMaster University
          </p>
          <p>
            My research focuses on platform economy and influencers' strategies
            decisions in livestream shopping.
          </p>
          <p>View my <a href="./static/Zhe_Betty_Ji_CV_.pdf" target="_blank">CV</a></p>
          <p>Email: jiz3@mcmaster.ca</p>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "home",
  data() {
    return {
      isShow: true
    };
  }
};
</script>

<style scoped>
#profile {
  /* text-align: center; */
  /* float: right;
  margin-right: 8rem;
  margin-top: 2rem; */
}
#introduce {
  text-align: left;
}
#introduce p {
  /* font: italic 1.1rem Georgia, serif; */
}
h1 {
  /* font: italic normal normal 2.5rem/1.4em "times new roman", times, serif; */
}
a {
  color: blue;
}
</style>
<style>
@keyframes side {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0);
  }
}

#introduce {
  animation-name: side;
  animation-duration: 1s;
}
#profile img{
  width: 230px;
  height: 295px;
  margin: 0 auto;
}
@media (max-width: 768px) {
  #profile img{
    
    width: 180px;
    height: 230px;
    
  }
  #profile {
    text-align: center;
  }

}
</style>



